import Vue from 'vue'
import Router from 'vue-router'
import shou from '@/components/shou'
import me from '@/components/me'
import house from '@/components/house'
import zufang from '@/components/zufang'
import remen from '@/components/shou/remen'
import you from '@/components/shou/you'
import quy from '@/components/house/quy'
import jiag from '@/components/house/jiag'
import fangx from '@/components/house/fangx'
import gengd from '@/components/house/gengd'
import xiangq from '@/components/house/xiangq'
import zufx from '@/components/zufang/zufx'
import ydeng from '@/components/me/ydeng'
import guanzhu from '@/components/me/guanzhu'
import deng from '@/components/me/deng'
import dengp from '@/components/me/dengp'
import dengy from '@/components/me/dengy'
import zhao from '@/components/me/zhao'
import zhu from '@/components/me/zhu'



Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'shou',
      redirect: '/shou',
    },
    {
      path: '/shou',
      name: 'shou',
      component: shou,
      children:[
      	{
		      path: '/',
		      name: 'remen',
		      redirect: '/show/remen',
		    },
		    {
		      path: '/show/remen',
		      name: 'remen',
		      component: remen,
		    },
		    {
		      path: '/show/you',
		      name: 'you',
		      component: you,
		    },
      ]
    },
    {
      path: '/me',
      name: 'me',
      component: me,
    },
    {
      path: '/house',
      name: 'house',
      component: house,
      children:[
		    {
		      path: '/house/fangx',
		      name: 'fangx',
		      component: fangx,
		    },
		    {
		      path: '/house/gengd',
		      name: 'gengd',
		      component: gengd,
		    },
		    {
		      path: '/house/jiag',
		      name: 'jiag',
		      component: jiag,
		    },
		    {
		      path: '/house/quy',
		      name: 'quy',
		      component: quy,
		    },
			]
    },
    {
      path: '/xiangq',
      name: 'xiangq',
      component: xiangq,
    },
     {
      path: '/ydeng',
      name: 'ydeng',
      component: ydeng,
    },
     {
      path: '/guanzhu',
      name: 'guanzhu',
      component: guanzhu,
    },
    {
      path: '/deng',
      name: 'deng',
      component: deng,
      children:[
      	{
		      path: '/',
		      name: 'dengp',
		      redirect: '/deng/dengp',
		    },
		    {
		      path: '/deng/dengy',
		      name: 'dengy',
		      component: dengy,
		    },
		    {
		      path: '/deng/dengp',
		      name: 'dengp',
		      component: dengp,
		    },
      ]
    },
    {
      path: '/zufx',
      name: 'zufx',
      component: zufx,
    },
    {
      path: '/zhao',
      name: 'zhao',
      component: zhao,
    },
    {
      path: '/zhu',
      name: 'zhu',
      component: zhu,
    },
    {
      path: '/zufang',
      name: 'zufang',
      component: zufang,
    },
  ]
})



